@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
.Navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 6vh;
}

.Navbar .logo {
    margin-right: 15px;
    padding: 0 13px;
    font-size: 22px;
    background-color: #eceff1;
    font-family: 'Roboto', sans-serif;
    height: 100%;
    display: flex;
    align-items: center;
}

.Navbar .logo a, .Navbar .logo a:visited {
    text-decoration: none;
    color: #333;
}

.slider {
    width: 350px;
    margin: 0 10px;
    display: inline-block;
}

.Navbar .rc-slider-track {
    background-color: transparent;
}

.Navbar .rc-slider-rail {
    height: 8px;
}

.Navbar .rc-slider-handle, .Navbar .rc-slider-handle:active, .Navbar .rc-slider-handle:focus, .Navbar .rc-slider-handle:hover {
    background-color: green;
    outline: none;
    border: green;
    box-shadow: none;
    width: 15px;
    height: 15px;
    margin-top: -4px;
}

.select-container {
    margin-right: 1.5rem;
}

.delete {
    margin-left: auto;
    padding: 0 1.2rem;
    font-size: 22px;
    background-color: #eb3d30;
    color: white;
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.delete svg {
    transition: all 0.2s ease-in-out;
    opacity: 0.5;
}

.delete:hover svg {
    transform: scale(1.2);
    opacity: 1;
}

.right-menu {
    display: flex;
    height: 100%;
    align-items: center;
}

/* medium screens */

@media screen and (max-width: 992px) {
    .slider-container {
        width: 100%;
        display: flex;
        align-items: center;
    }
    .slider {
        width: 80%;
    }
    .slider-container>span {
        display: none;
    }
    .delete {
        padding: 0.8rem;
    }
    .select-container {
        margin-right: 1rem;
    }
}

/* small screens */

@media screen and (max-width: 576px) {
    .Navbar {
        justify-content: flex-end;
    }
    .Navbar .logo {
        display: none;
    }
    .slider {
        width: 85%;
    }
}